import ReactDOM, { Component } from 'react';
import React from 'react';
import firebase, {auth, provider} from './firebase_creds'
import styles from './SignIn.module.css';
import image from './GoogleSign.png'
import {connect} from 'react-redux';

class App extends Component {

  state = {
    wrong: ''
  }
  componentDidMount()
  {

    /* There are 2 initial authenication checks,
    1st we check if there's a JWT token inside
    the window.session storage, if not then we
    move on to the next check which is Firebase,
    of which does it's magic automatically
    */

    //1st check
    if(window.sessionStorage.getItem('token') != null)
    {
      window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/home';
    }

    //2nd check
    firebase.auth().onAuthStateChanged((user) => {

      //This code is for when a user signs out:
      if(window.sessionStorage.getItem('email') != '' && user == null)
      {
          window.sessionStorage.removeItem('email')
          window.sessionStorage.removeItem('token')
      }

      //When a user signs in:
      if(user != null)
      {
        console.log(user);

        /*In here make an API call, */
        this.loginAPICall(true, user.email);

        //window.location.replace('localhost:3000/')
      }
    })

  }

  loginAPICall = (fireb=false, email='', password='') => {

    fetch('http://petapplicationbackend-env.eba-impnejui.us-east-2.elasticbeanstalk.com/login', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          firebase: fireb,
          email:email,
          password: password
        })
    }).then(response => response.json())
    .then(res => {
    console.log(res);
      if(res['message'] == "doesnt exist")
      {
        window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/register';
      }

      if(res['message'] == 'wrong creds')
      {
        this.setState({wrong: 'Wrong Username/Password'})
      }

      if(res['message'] == "success"){
        this.setState({wrong: ''})
        window.sessionStorage.setItem('token', res['token']);
        window.sessionStorage.setItem('email', email);
        window.sessionStorage.setItem('name', res['name']);
        window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/home';
      }

    })
  }

  signInPopUP = () => {
    auth.signInWithPopup(provider);
  }

  login = () => {
    let email = this.refs['email'].value;
    let password = this.refs['password'].value;
    this.loginAPICall(false, email, password);
  }
  // Quick Sign Out button {() => {auth.signOut()}}

  /*
  <button onClick={()=>{window.sessionStorage.setItem('email', 'sasdwa')}}>OH</button>
  */


  render()
  {
    return (
      <div>
      <h2 style={{textAlign: 'center'}}>Pet Application</h2>
      <div className={styles.login_box}>
        <p>Welcome Back</p>
        <p style={{color:'red'}}>{this.state.wrong}</p>
        <input ref='email'placeHolder='email'className={styles.input}/>
        <input ref='password'placeHolder='password'
        type='password'className={styles.input}/>
        <button className={styles.Login_button}
        onClick={() => {this.login()}}> Login </button>
        <p> OR </p>
        <input type='image' src={image} onClick={this.signInPopUP}/>
        <p> Don't Have An Account? <span id='hoverable_text' style={{
        textDecoration: 'none', color:'grey'}}
        onClick={()=>{window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/register';}}>Register</span> </p>
      </div>
      </div>
    );
  }
}



export default App
