import React from 'react'
import moment from 'moment'
import 'moment-timezone'
import styles from './Appointment.module.css'

function Appointment(props)
{
  console.log(props);

  let date = moment.utc(`${props.time}`).local().format('YYYY-MM-DD HH:mm:ss');
  let good_date = moment.utc(date).local().format('MM-DD-YYYY hh:mm A');
//  let hours = moment(good_date).format('HH:mm A');

  /*address: info['address'],
  client_name: info['client_name'],
  receiver_email: info['receiver_email'],
  sender_email: info['sender_email'],
  location: info['location']
  */
  return (
    <div className={styles.box}>
      <h4>Client's Name: {props.client_name}</h4>
      <h4>Where: {props.location}</h4>
      <h4>Date Booked: {good_date}</h4>
    </div>
  )
}

export default Appointment
