import ReactDOM, { Component } from 'react';
import React from 'react';
import styles from './Home.module.css'
import Appointment from './Components/Appointment'
import image from './plus.png'
import firebase, {auth, provider} from './firebase_creds'

class Home extends Component
{
  state = {
    appointments: []
  }

  componentDidMount()
  {

    fetch('http://petapplicationbackend-env.eba-impnejui.us-east-2.elasticbeanstalk.com/get_appointments', {
      method: 'post',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        email: window.sessionStorage.getItem('email'),
      })
    }).then(response => response.json())
    .then(data => {
      this.setState({appointments: data});
    });

  }

  signOut = () => {
    auth.signOut();
    window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/login';
    window.sessionStorage.removeItem('token');
    window.sessionStorage.removeItem('email');
    window.sessionStorage.removeItem('name');
  }

  render()
  {
    /*address: info['address'],
    client_name: info['client_name'],
    receiver_email: info['receiver_email'],
    sender_email: info['sender_email'],
    location: info['location']
    */

      return (
        <div className={styles.back_div}>

          <button onClick={()=>{this.signOut()}}>sign out</button>

          <div className={styles.appointments_box}
          style={{padding:'10px'}}>
            {this.state.appointments.map(data => {
              return(<Appointment client_name={data['client_name']}
              location={data['location']} time={data['time']} />)
            })}

          </div>

          <div className={styles.appointments_box}
          style={{justifyContent: 'center'}}>
            <img src={image} width='128'
            height='128' id='hoverable_text'
            onClick={()=>{window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/create_appointment'}}/>
            <h2>Make Appointment</h2>
          </div>
        </div>
      )
  }
}

export default Home;
