import ReactDOM, { Component } from 'react';
import React from 'react';
import styles from './SignIn.module.css'

class CreateAppointment extends Component
{
  state = {
    error: ""
  }

  submitForm = () => {
    let good_form = true;

    Object.keys(this.refs).map(key => {
      if(this.refs[key].value == ''
      && key != 'form')
      {
        this.setState({error: "All fields must be filled"})
        good_form = false;
      }
    });

    if(good_form)
    {
      fetch('http://petapplicationbackend-env.eba-impnejui.us-east-2.elasticbeanstalk.com/create_appointment', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          sender_email: window.sessionStorage.getItem('email'),
          receiver_email: this.refs.recipient.value,
          client_name: window.sessionStorage.getItem('name'),
          location: this.refs.location.value
        })
      }).then(response => response.json())
      .then(res => {
        console.log(res);
        if(res['message'] == 'not found')
        {
          this.setState({error: 'Failed To Make Appointment, Recipient Email Not Found'});
        }else {
          this.setState({error: 'Appointment Made!'});

        }
      });
    }
  }

  render()
  {
    return (

      <div className={styles.login_box}>

        <input ref='recipient' type='text'
        placeHolder='Email of recipient'
        className={styles.input}/>

        <input ref='location' type='text'
        placeHolder='Please Enter Appointment Location'
        className={styles.input}/>


        <p style={{marginBottom:'5%', color: 'red'}}>{this.state.error}</p>

        <input className={styles.Login_button} type='submit'
        onClick={this.submitForm}/>
      </div>)
  }
}

export default CreateAppointment;
