import styles from './SignIn.module.css'
import ReactDOM, { Component } from 'react';
import React from 'react';
import firebase, {auth, provider} from './firebase_creds'

class SignUp extends Component {

  state = {
    name: '',
    email: '',
    pass1: '',
    pass2: '',
    matching: ''
  }

  submitForm = () => {
    let good_form = true;

    //1st check if the fields are empty
    //there should be no empty fields.
    Object.keys(this.refs).map(key => {
      if(this.refs[key].value == ''
      && key != 'form')
      {
        this.setState({matching: "All fields must be filled"})
        good_form = false;
      }
    });

    //Check the passwords
    if(!(this.refs['pass1'].value == this.refs['pass2'].value))
    {
      this.setState({matching: "Passwords don't match"})
      good_form = false;
    }

    //Check email
    const reg = /\@\w+\.\w+/gi
    if(!reg.test(this.refs['email'].value))
    {
        this.setState({matching: "Email must be in standard address@host.com form"})
        good_form = false;
    }

    if(good_form)
    {
      this.setState({matching: ""})

      fetch('http://petapplicationbackend-env.eba-impnejui.us-east-2.elasticbeanstalk.com/register', {
          method: 'post',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            name: this.refs['name'].value,
            email: this.refs['email'].value,
            password: this.refs['pass1'].value,
          })
      }).then(response => response.json())
      .then(data => {
        console.log(data);
        if(data['message'] == 'Already Exists')
        {
          this.setState({matching: "Email has already been used"})
        }else{
          window.sessionStorage.setItem('email', this.refs['email'].value);
          window.sessionStorage.setItem('name', this.refs['name'].value);
          window.sessionStorage.setItem('token', data['token']);
          auth.signOut();
          window.location.href = 'https://main.d3nkdvr7c9taa8.amplifyapp.com/home';
        }
      })
    }

  }

  render()
  {

    return (
      <div>
        <h2></h2>
        <div className={styles.login_box}>

          <input ref='name' required className={styles.input}
          type='text' placeHolder='First Name And Last Name'/>

          <input ref='email'required className={styles.input} type='email'
          placeHolder='Email'/>

          <input ref='pass1'required className={styles.input}
          type='password' placeHolder='Password'/>


          <input ref='pass2'required className={styles.input}
          style={{marginBottom: '3%'}}
          type='password' placeHolder='Confirm Password'/>

          <p style={{marginBottom:'5%', color: 'red'}}>{this.state.matching}</p>

          <input className={styles.Login_button} type='submit'
          onClick={this.submitForm}/>
        </div>
        </div>
    )
  }
}

export default SignUp
